import React, { useState, useEffect } from "react"
import { useHistory, Link } from "react-router-dom"
import axios from "axios"
import "./css/dashboard.scss"

function OrdersDashboard() {
  const history = useHistory()
  const jwt = localStorage.getItem("jwt")
  const [orders, setOrders] = useState()
  const [status, setStatus] = useState("")

  if (!jwt || JSON.parse(jwt).user.username !== "admin") history.push("/")

  const handleChange = ({ target }) => setStatus(target.value)

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/book/order?status=${status}`)
      .then(({ data }) => setOrders(data.orders))
  }, [status])

  const handleClick = async ({ target }) => {
    const [status, _id] = target.value.split("-")
    try {
      await axios.put(`${process.env.REACT_APP_API_URL}/book/order`, {
        status,
        _id,
      })
      alert("Order successfully updated")
    } catch (err) {
      alert("Order couldn't be updated")
    }
  }

  if (!orders) return <div></div>

  return (
    <div>
      <h1>Dashboard</h1>
      <div>
        <label>Status: </label>
        <select onChange={handleChange}>
          <option value="">All</option>
          <option value="Processing">Processing</option>
          <option value="Delivered">Delivered</option>
          <option value="Cancelled">Cancelled</option>
        </select>
        <div>
          <table class="table table-striped">
            <thead>
              <tr>
                <th className="order-column-id" scope="col">
                  #
                </th>
                <th scope="col">Username</th>
                <th scope="col">Books</th>
                <th scope="col">Price</th>
                <th scope="col">County</th>
                <th scope="col">City</th>
                <th scope="col">Address</th>
                <th scope="col">Buttons</th>
              </tr>
            </thead>
            <tbody>
              {orders.map((order) => (
                <tr>
                  <Link to={`/user/order/${order._id}`} className="order-link">
                    <th className="order-column-id" scope="row">
                      {order._id}
                    </th>
                  </Link>
                  <td>{order.orderedBy}</td>
                  <td>{order.numOfBooks}</td>
                  <td>{order.price}</td>
                  <td>{order.county}</td>
                  <td>{order.city}</td>
                  <td>{order.address}</td>
                  <td>
                    <button
                      className="order-buttons order-buttons-delivered"
                      value={`Delivered-${order._id}`}
                      onClick={handleClick}
                    >
                      ✓
                    </button>
                    <button
                      className="order-buttons order-buttons-cancel"
                      value={`Cancelled-${order._id}`}
                      onClick={handleClick}
                    >
                      x
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

export default OrdersDashboard
