import React, { useState, useEffect } from "react"
import { useParams, useHistory } from "react-router-dom"
import { useForm } from "react-hook-form"
import { bookLink, imageSrc } from "../components/links"
import { addToCart } from "../components/BookMap"
import ReactStars from "react-rating-stars-component"
import axios from "axios"
import "./css/Book.scss"

function Book() {
  const [book, setBook] = useState()
  const [display, setDisplay] = useState("none")
  const [reviewDisplay, setReviewDisplay] = useState("none")
  const [rating, setRating] = useState(0)
  const [inputValue, setInputValue] = useState("")
  const [error, setError] = useState({ color: "", message: "" })
  const jwt = JSON.parse(localStorage.getItem("jwt"))
  const user = jwt ? jwt.user : {}
  const { handleSubmit, register } = useForm()

  const { id } = useParams()
  const history = useHistory()

  useEffect(() => {
    axios.get(bookLink(id)).then(({ data }) => {
      setBook(data)
      if (data.rating) setRating(data.rating)
    })
  }, [reviewDisplay])

  const ratingChange = (newRate) => setRating(newRate)

  const ratingClicked = () => setReviewDisplay("block")

  const submitReview = async ({ comment }) => {
    const { username } = user

    const sendData = {
      username: username || "unkown",
      comment,
      rating,
    }

    const { data } = await axios.post(
      `${process.env.REACT_APP_API_URL}/book/review/${book._id}`,
      sendData
    )

    if (data.error) console.log(data.error)
    setReviewDisplay("none")
  }

  const addToWishlist = async ({ wishlist }) => {
    const { username } = user
    const sendData = {
      book: book._id,
      name: wishlist,
      username,
    }

    try {
      if (!sendData.name) throw new Error("Please check a wishlist")

      const { data } = await axios.post(
        `${process.env.REACT_APP_API_URL}/wishlist/book`,
        sendData
      )

      if (data.error) throw new Error(data.error)

      const { message } = data
      setError({ message, color: "green" })
      if (!user.wishlists) {
        user.wishlists = [sendData.name]
        const { token } = JSON.parse(localStorage.getItem("jwt"))
        localStorage.setItem("jwt", JSON.stringify({ token, user }))
      } else if (!user.wishlists.includes(sendData.name)) {
        user.wishlists.push(sendData.name)
        const { token } = JSON.parse(localStorage.getItem("jwt"))
        localStorage.setItem("jwt", JSON.stringify({ token, user }))
      }
    } catch ({ message }) {
      setError({ message, color: "red" })
    }
  }

  if (!book) return <div></div>

  return (
    <div className="book-parent">
      <div className="book-presentation">
        <div className="col-lg-5">
          <img src={imageSrc(book)} className="book-presentation-image" />
        </div>
        <div className="book-presentation-info col-lg-7">
          <div
            style={{
              width: 195,
              display: "flex",
              alignItems: "center",
            }}
          >
            <div
              onClick={ratingClicked}
              style={{
                width: 108,
              }}
            >
              <ReactStars
                count={5}
                onChange={ratingChange}
                size={24}
                activeColor="#ffd700"
                classNames="book-presentation-rating"
              />
            </div>
            <a href="#review" style={{ color: "gray" }}>
              ({book.reviews.length} review{book.reviews.length !== 1 && "s "})
            </a>
          </div>
          <h5 className="book-presentation-info-title">{book.title}</h5>
          <h6 className="book-presentation-info-author">{book.author}</h6>
          <h4 className="book-presentation-info-price">
            {book.price}
            <sup>00</sup> lei
          </h4>
          <div className="book-presentation-buttons">
            <button
              onClick={addToCart}
              value={book._id}
              className="btn btn-primary book-presentation-buttons-add-cart"
            >
              Add to Cart
            </button>
            <button
              onClick={(ev) => {
                ev.preventDefault()
                if (!user.username) history.push("/signin")

                setDisplay("block")
              }}
              value={book._id}
              className="btn btn-primary book-presentation-buttons-add-wishlist"
            >
              Add to wishlist
            </button>
          </div>
        </div>
      </div>
      <div className="book-description">
        <h4 className="book-description-title">Description</h4>
        <p className="book-description-text">{book.description}</p>
      </div>
      <div className="book-details">
        <h4 className="book-details-title">Details</h4>
        <table className="table table-striped">
          <tbody>
            {["title", "author", "category", "pages", "publisher", "year"].map(
              (item) => (
                <tr className="book-details-spec">
                  <th className="book-details-spec-item">
                    {item[0].toUpperCase() + item.slice(1, item.length)}
                  </th>
                  <td className="book-details-spec-info">{book[item]}</td>
                </tr>
              )
            )}
          </tbody>
        </table>
      </div>
      <div className="book-reviews" id="review">
        <h4 className="book-reviews-title">Reviews</h4>
        {book.reviews > 0 ? (
          book.reviews.map((review) => (
            <div>
              <h4>{review.username}</h4>
              <ReactStars
                count={5}
                value={review.rating}
                size={24}
                classNames="book-presentation-rating"
              />
              <p>{review.comment}</p>
            </div>
          ))
        ) : (
          <h5>No reviews to this book</h5>
        )}
      </div>
      <div className="book-wishlist" style={{ display: reviewDisplay }}>
        <div className="book-wishlist-top">
          <h2>Write a review for your rating</h2>
          <button
            onClick={(ev) => {
              ev.preventDefault()
              setReviewDisplay("none")
            }}
            className="book-wishlist-top-button"
          >
            <span>X</span>
          </button>
        </div>
        <form onSubmit={handleSubmit(submitReview)}>
          <textarea name="comment" ref={register} className="form-control" />
          <button className="btn btn-primary" style={{ marginTop: 20 }}>
            Submit
          </button>
        </form>
      </div>
      <div
        style={{
          display,
        }}
        className="book-wishlist"
      >
        <div className="book-wishlist-top">
          <h2>Add to wishlist</h2>
          <button
            onClick={(ev) => {
              ev.preventDefault()
              setDisplay("none")
            }}
            className="book-wishlist-top-button"
          >
            <span>X</span>
          </button>
        </div>
        <form
          onSubmit={handleSubmit(addToWishlist)}
          className="book-wishlist-form"
        >
          {user.wishlists &&
            user.wishlists.map((wishlist) => (
              <div>
                <input
                  type="radio"
                  value={wishlist}
                  name="wishlist"
                  id={wishlist}
                  ref={register}
                />
                <label htmlFor={wishlist} className="book-wishlist-form-label">
                  {wishlist}
                </label>
              </div>
            ))}
          <input
            type="radio"
            name="wishlist"
            id="write"
            value={inputValue}
            ref={register}
          />
          <label for="write" className="book-wishlist-form-label">
            <input
              type="text"
              onChange={({ target: { value } }) => setInputValue(value)}
              className="book-wishlist-form-input"
            />
          </label>
          <p style={{ color: error.color }}>{error.message}</p>
          <button className="btn btn-primary book-wishlist-form-button">
            Submit
          </button>
        </form>
      </div>
    </div>
  )
}

export default Book
