import React, { useState, useEffect } from "react"
import axios from "axios"
import { useParams, useHistory, Link } from "react-router-dom"
import { BooksMap, addToCart, numberList } from "../components/BookMap"
import { getBooksLink, imageSrc } from "../components/links"
import Filters from "./Filters"
import "./css/Books.scss"

function GetBooks() {
  const [books, setBooks] = useState([])
  const [pages, setPages] = useState(0)
  const { search, filters, page } = useParams()
  const jwt = JSON.parse(localStorage.getItem("jwt"))
  const username = jwt ? jwt.user.username : null

  useEffect(() => {
    const url = getBooksLink(filters, search, page)

    axios
      .get(url)
      .then(({ data }) => setBooks(data))
      .catch((err) => console.log(err.message))

    axios
      .get(`${process.env.REACT_APP_API_URL}/book/pages/2`)
      .then(({ data }) => setPages(data.message))
  }, [filters, search, page])

  return (
    <div className="books">
      <Filters />

      <div className="books-map col-9">
        {books &&
          books.map((book) => (
            <div
              className="book"
              style={
                !book.amount ? { opacity: 0.5, cursor: "not-allowed" } : {}
              }
            >
              <Link to={`/book/${book.title}`} className="book-link">
                <img
                  src={`${process.env.REACT_APP_API_URL}/book/image/${book._id}`}
                  className="book-link-img"
                />
              </Link>
              <div className="book-link-div">
                <Link
                  to={`/book/${book.title}`}
                  style={{ color: "black" }}
                  className="book-link"
                >
                  <h4 className="book-link-title">{book.title}</h4>
                  <h6 className="book-link-author">{book.author}</h6>
                </Link>
                <p>
                  {book.price}
                  <sup>00</sup> lei
                </p>

                {username === "admin" ? (
                  <Link
                    to={`/admin/edit-book/${book._id}`}
                    className="btn btn-primary"
                  >
                    Edit Book
                  </Link>
                ) : (
                  <button
                    onClick={addToCart}
                    value={book._id}
                    className="btn btn-primary"
                    style={{
                      cursor: book.amount ? "pointer" : "not-allowed",
                    }}
                  >
                    Add cart
                  </button>
                )}
              </div>
            </div>
          ))}
      </div>

      {/* {pages > 0 && (
          <div className="book-pages">
            {numberList(pages).map((page) => (
              <Link className="book-pages-button" to={`/page=${page}`}>
                {page}
              </Link>
            ))}
          </div>
        )} */}
    </div>
  )
}

export default GetBooks
