import React, { useState, useEffect } from "react"
import { Link, useParams } from "react-router-dom"
import axios from "axios"
import "./css/Order.scss"
import { imageSrc } from "../components/links"
import * as moment from "moment"

function Order() {
  const { id } = useParams()
  const [order, setOrder] = useState()
  const [books, setBooks] = useState()
  const [message, setMessage] = useState({ text: "", message: "" })

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/book/order/id=${id}`)
      .then(({ data: { order } }) => {
        setOrder(order)
        axios
          .get(
            `${
              process.env.REACT_APP_API_URL
            }/book?fields=id,title,author,price&page&_id=${Object.keys(
              order.cart
            )}`
          )
          .then(({ data }) => setBooks(data))
          .catch(({ message }) => console.log(message))
      })
      .catch(({ message }) => console.log(message))
  }, [])

  const handleClick = async (ev) => {
    ev.preventDefault()
    const { _id } = order

    try {
      await axios.put(`${process.env.REACT_APP_API_URL}/book/order`, {
        _id,
        status: "Cancelled",
      })

      setMessage({ text: "Order cancelled", color: "green" })
    } catch (err) {
      setMessage({
        text: "We can't solve it right now, please try again later",
        color: "red",
      })
    }
  }

  if (!order) return <div></div>

  const { cart } = order
  return (
    <div className="order">
      <div>
        <h4>{order._id}</h4>
      </div>
      <div>
        <h5>Address</h5>
        <p>County: {order.county}</p>
        <p>City: {order.city}</p>
        <p>Street: {order.address} </p>
        <p>
          Price: {order.price}
          <sup>00</sup> RON
        </p>
        <p>Date: {moment(order.createdAt).format("YYYY-MM-DD")}</p>
        <p>Status: {order.status}</p>
      </div>
      <div className="order-books">
        <h4 className="order-books-title">Books({order.numOfBooks})</h4>
        <div className="order-books-map">
          {books &&
            books.map((book) => (
              <div className="order-books-item">
                <h3 className="order-books-item-multiple">
                  {cart[book._id]} X
                </h3>
                <Link to={`/book/${book.title}`}>
                  <img src={imageSrc(book)} style={{ height: 100 }} />
                </Link>
              </div>
            ))}
        </div>
      </div>
      <div className="order-cancel">
        <button
          className="btn btn-raised btn-primary "
          onClick={handleClick}
          value={order._id}
        >
          Cancel order
        </button>
        <p style={{ color: message.color, marginTop: 10 }}>{message.text}</p>
      </div>
    </div>
  )
}

export default Order
