import React, { useEffect, useState } from "react"
import { useHistory, Link } from "react-router-dom"
import { getBooksLink, addToCart } from "../components/links"
import axios from "axios"
import "./css/Wishlist.scss"

function Wishlist() {
  const history = useHistory()

  const {
    user,
    user: { wishlists },
  } = JSON.parse(localStorage.getItem("jwt"))

  const [books, setBooks] = useState()
  const [wishlist, setWishlist] = useState(user.wishlists[0])

  useEffect(() => {
    if (!user) history.push("/signin")

    axios
      .get(`${process.env.REACT_APP_API_URL}/wishlist/one/${wishlist}`)
      .then(({ data }) => {
        const ids = data.wishlist.books

        axios
          .get(getBooksLink(`_id=${ids.join(",")}`))
          .then(({ data }) => setBooks(data))
      })
      .catch(({ message }) => console.log(message))
  }, [wishlist])

  if (wishlists.length === 0) return <div>No wishlists</div>
  return (
    <div className="wishlist-list">
      <select
        className="form-control"
        onChange={({ target: { value } }) => setWishlist(value)}
      >
        {wishlists.map((wishlist) => (
          <option>{wishlist}</option>
        ))}
      </select>
      {books && (
        <div className="wishlist-books-map">
          {books.map((book) => (
            <div
              className="wishlist-book"
              style={
                !book.amount ? { opacity: 0.5, cursor: "not-allowed" } : {}
              }
            >
              <Link to={`/book/${book.title}`} className="wishlist-book-link">
                <div className="wishlist-book-link-img-div">
                  <img
                    src={`${process.env.REACT_APP_API_URL}/book/image/${book._id}`}
                    className="wishlist-book-link-img"
                  />
                </div>
              </Link>
              <div className="wishlist-book-link-div">
                <Link
                  to={`/book/${book.title}`}
                  style={{ color: "black" }}
                  className="wishlist-book-link"
                >
                  <h4 className="wishlist-book-link-title">{book.title}</h4>
                  <h6 className="wishlist-book-link-author">{book.author}</h6>
                </Link>
                <p className="wishlist-book-link-price">
                  {book.price}
                  <sup>00</sup> lei
                </p>

                <button
                  onClick={addToCart}
                  value={book._id}
                  className="btn btn-primary"
                  style={{
                    cursor: book.amount ? "pointer" : "not-allowed",
                  }}
                >
                  Add cart
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

export default Wishlist
