exports.getBooksLink = (filters, search, page) => {
  return `${
    process.env.REACT_APP_API_URL
  }/book?fields=id,title,author,price,amount&page=${page || 1}&limit=8${
    search ? `&search=${search}` : filters ? `&${filters}` : ""
  }`
}
exports.filtersLink = `${process.env.REACT_APP_API_URL}/book/filters`

exports.bookLink = (id) => `${process.env.REACT_APP_API_URL}/book/id=${id}`

exports.imageSrc = ({ _id }) =>
  `${process.env.REACT_APP_API_URL}/book/image/${_id}`

exports.cartLink = (cart) =>
  `${
    process.env.REACT_APP_API_URL
  }/book?fields=id,title,author,price,amount&_id=${Object.keys(cart).join(",")}`
